.alert-container{position:fixed;top:20px;width:100%;display:flex;justify-content:center;z-index:1003}.alert-container .alert{position:fixed;padding:10px;background:rgba(0,0,0,.8);color:#fff;border-radius:5px;box-shadow:0 1px 10px rgba(0,0,0,.2);transition:.3s all;opacity:0;transform:translate(0, -100px);display:flex;align-items:center}.alert-container .alert.show{opacity:1;transform:translate(0, 0)}.alert-container .success{background:#009b27}.alert-container .success::before{content:"";font-family:FontAwesome;margin-right:15px;font-size:20px}.alert-container .error{background:red}.alert-container .error::before{content:"";font-family:FontAwesome;margin-right:15px;font-size:20px}.alert-container .warning::before{content:"";font-family:FontAwesome;margin-right:15px;font-size:20px}